export default {
    name: 'app-search-for',

    props: {
        value: {
            type: Object,
            default: function() {
                return {
                    background: `#efeff4`,
                    color: '#ffffff',
                    placeholder: '搜索',
                    radius: 28,
                    textColor: '#999999',
                    textPosition: `center`,
                }
            }
        }
    },
    data() {
        return {
            newData: this.value
        };
    },
    watch: {
        value: {
            handler(newData) {
                this.newData = newData;
            },
            deep: true
        }
    },
    methods: {
        jump_route() {
            uni.navigateTo({
                url: '/pages/search/search'
            });
        }
    }
}
